.portfolio__footer {
	display: flex;
	width: 100%;
	justify-content: center;
	padding: 1rem;
	background-color: var(--color-footer);
}

.portfolio__footer-links {
	flex-direction: row;
	justify-content: center;
	align-content: center;
}

.portfolio__footer-links a {
	display: flex;
	justify-content: center;
	align-items: center;
	text-decoration: underline;
	color: var(--color-text);
	padding: 0.3rem 0;
}

.portfolio__footer-links a:hover {
	color: #FF4820;
}

.portfolio__footer-links p {
	color: var(--color-text);
	font-family: var(--font-family);
	font-size: 20px;
	font-weight: 300;
	margin-bottom: 1rem;
}

.portfolio__footer-links-logos {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.portfolio__footer-links-logos img {
	height: 40px;
	width: 40px;
}

.portfolio__footer-links-logos img:hover {
	transform: scale(1.1);
	filter: invert(0.75);
}