.portoflio__header {
	display: flex;
}

.portfolio__header-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-bottom: 3rem;
}

.portfolio__header-container h1 {
	color: var(--color-text);
	font-family: var(--font-family);
	font-size: 2rem;
}

.portfolio__header-container p {
	color: var(--color-text);
	font-family: var(--font-family);
	font-size: 1rem;
}