.portfolio__about {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	font-family: var(--font-family);
}

.portfolio__about h1 {
	color: var(--color-text);
	font-size: 2.5rem;
	font-weight: 300;
	align-self: center;
}

.portfolio__about p {
	color: var(--color-text);
	font-size: 4rem;
	font-weight: 300;
	width: 75rem;
}


.portfolio__about-container {
	display: flex;
	justify-content: center;
	align-self: center;
}

.portfolio__about-container-textbox {
	display: inline-flex;
	width: 25%;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 0 2rem;
}

.portfolio__about-container-textbox p {
	width: 100%;
	font-size: 1.5rem;
	font-weight: 400;
	margin-bottom: 1rem;
	align-self: center;	
}

.portfolio__about-container-frame {
	display: flex;
	overflow: hidden;
	border-radius: 25rem;
	// margin-right: 50px;	
}

.portfolio__about-container-frame img {
	width: 100%;
	height: 100%;
	overflow: hidden;
	width: calc(min(500px, 100vh) * 0.8);
	transition: transform 0.25s ease, filter 0.25s ease;
	filter: saturate(0.9);
}

.portfolio__about-container-frame img:hover {
	filter: saturate(1);
	transform: scale(1.1);
}

.portfolio__about-links {
	display: flex;
	justify-content: center;
	align-items: center;
}

.portfolio__about-links p {
	color: white;
	font-size: 1rem;
	font-weight: 400;
	margin: 0 1rem;
}

.portfolio__about-links img {
	width: 40px;
	height: 40px;
	object-fit: cover;
	filter: invert(.5);
	cursor: pointer;
}

.portfolio__about-links img:hover {
	filter: invert(1);
}

@media screen and (max-width: 1050px) {
	.portfolio__about {
	}

	.portfolio__about object {
		// justify-content: center;
		align-self: center;
		width: 45rem;
	}

	.portfolio__about-container {
		flex-direction: column;
		align-items: center;
	}

	.portfolio__about-container p {
		width: 100%;
		text-align: center;
	}

	.portfolio__about-container-textbox{
		margin-top: 1rem;
		width: 35%;
	}
	
	.portfolio__about-container-frame{
		margin: 0;
		max-width: 50%;
	}
	
	.portfolio__about-container-frame img {
		width: calc(min(400px, 40vh));
	}
}

@media screen and (max-width: 700px) {
	.portfolio__about-container-frame img {
		width: calc(min(250px, 40vh));
	}

	.portfolio__about-container p{
		font-size: 1.1rem;
	}
}

@media screen and (max-width: 550px) {
	.portfolio__about-container-frame img {
		width: calc(min(150px, 40vh));
	}

	.portfolio__about-container p{
		font-size: .9rem;
	}	
}