.portfolio__design {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-family: var(--font-family);
	color: var(--color-text);
}

.portfolio__design-container {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-items: center;
	gap: 10px;
}

.portfolio__design-container h1 {
	font-size: 35px;
}

.portfolio__design-container-frame {
	overflow: hidden;
	display:inline-block;
}

.portfolio__design-container-frame img {
	width: 100%;
	max-width: 500px;
	object-fit: cover;
}

.portfolio__design-container-desc {
	margin-bottom: 2rem;
}

.portfolio__design-container-desc h1 {
	font-size: 30px;
}

.portfolio__design-container-desc p {
	font-size: 20px;
}

@media screen and (max-width: 1050px) {
	.portfolio__design-container {
		grid-template-columns: 1fr;
	}
}

@media screen and (max-width: 700px) {
	.portfolio__design-container-frame {
		max-width: 100%;
		height: calc(width/1.5);
	}
}

