.portfolio__illustration {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-family: var(--font-family);
	color: var(--color-text);
}

.portfolio__illustration-container-desc {
	margin-bottom: 2rem;
}

.portfolio__illustration-container-desc h1 {
	font-size: 30px;
}

.portfolio__illustration-container-desc p {
	font-size: 20px;
}

.portfolio__illustration-container {
	display: flex;
	margin-bottom: 2rem;
}

.portfolio__illustration-container-frame {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-items: center;
	gap: 10px;
	width: 100%;
	height: 100%;
	align-items: center;
	border-radius: 5px;
	overflow: hidden;
}

.portfolio__illustration-container-frame img {
	width: 100%;
	max-width: 500px;
	object-fit: cover;
}

@media screen and (max-width: 1050px) {
	.portfolio__illustration-container-frame {
		grid-template-columns: 1fr;
	}
}

@media screen and (max-width: 700px) {
	.portfolio__illustration-container-frame {
		max-width: 100%;
		height: calc(width/1.5);
	}
}

