.portfolio__card {
	display: flex;
	flex-direction: column;
	position: relative;
	width: 250px;
	height: 300px;
	font-family: var(--font-family);
	align-items: center;
	justify-content: center;
	overflow: hidden;
	margin: 0 1rem;
	border-radius: 24px;
	transition: all .7s ease-in-out;
}
.portfolio__card.inactive{	
	background: var(--color-blog);
}

.portfolio__card.active{
	background: rgb(201, 201, 201);
	cursor: default;
}

.portfolio__card-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}

.portfolio__card.inactive .portfolio__card-row {
	display: none;
}

.portfolio__card.active .portfolio__card-row {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.portfolio__card-row img:hover {
	filter: invert(1);
	transition: all .1s ease-in-out;
}


.portfolio__card.inactive .portofolio__card-frame {
	display: inline-block;
}

.portfolio__card-frame img {
	cursor: pointer;
}

.portfolio__card.active .portfolio__card-frame img {
	display: none;
}

.portfolio__card.inactive .portfolio__card-frame img {
	width: 100%;
	height: 200px;
	object-fit: cover;
}

.portfolio__card.inactive:hover {
	transform: scale(1.05);
}

.portfolio__card.inactive:hover .portfolio__card-frame img {
	filter: saturate(1.1);
}

.portfolio__card.inactive h1{
	color: var(--color-text);
	font-size: 1.2rem;
	font-weight: 600;
}

.portfolio__card.active h1 {
	display: none;
}

.portfolio__card.inactive h2 {
	display:none;
}

.portfolio__card.active h2 {
	color: #FF4820;
	font-size: 1rem;
	font-weight: 400;
	text-decoration-line: underline;
	// margin: 1rem 0;
}

.portfolio__card.active h2:hover {
	color: var(--color-text);
}

.portfolio__card.inactive p {
	display: none;
}

.portfolio__card.active p {
	color: black;
	font-size: 0.8rem;
	font-weight: 400;
	margin: 1rem 0;
	padding: 0 1rem;
}

