* {
	box-sizing: border-box;
	/* padding: 0; */
	margin: 0;
	scroll-behavior: smooth;
}

body{
	background: var(--color-bg);
}

a{
	color: unset;
	text-decoration: none;
}

.vertical__bg {
	background-color: white;
	width: 50%;
	height: 100%;
}

.gradient__bg{
	/* ff 3.6+ */
	background:-moz-linear-gradient(357deg, rgba(247, 247, 247, 1) 0%, rgba(247, 247, 247, 1) 0%, rgba(247, 139, 35, 1) 99%); 

	/* safari 5.1+,chrome 10+ */
	background:-webkit-linear-gradient(357deg, rgba(247, 247, 247, 1) 0%, rgba(247, 247, 247, 1) 0%, rgba(247, 139, 35, 1) 99%);

	/* opera 11.10+ */
	background:-o-linear-gradient(357deg, rgba(247, 247, 247, 1) 0%, rgba(247, 247, 247, 1) 0%, rgba(247, 139, 35, 1) 99%);

	/* ie 6-9 */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#F7F7F7', endColorstr='#F78B23', GradientType=1 );

	/* ie 10+ */
	background:-ms-linear-gradient(357deg, rgba(247, 247, 247, 1) 0%, rgba(247, 247, 247, 1) 0%, rgba(247, 139, 35, 1) 99%);

	/* global 94%+ browsers support */
	/* background:linear-gradient(357deg, rgba(247, 247, 247, 1) 0%, rgba(247, 247, 247, 1) 0%, rgba(247, 139, 35, 1) 99%); */
}

.gradient__text{
	background:var(--gradient-text);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.section__padding {
	padding: 4rem 6rem;
}

.section__margin {
	margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
			animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
	0% {
	  -webkit-transform: scale(0.5);
			  transform: scale(0.5);
	}
	100% {
	  -webkit-transform: scale(1);
			  transform: scale(1);
	}
  }
  @keyframes scale-up-center {
	0% {
	  -webkit-transform: scale(0.5);
			  transform: scale(0.5);
	}
	100% {
	  -webkit-transform: scale(1);
			  transform: scale(1);
	}
}  
  
@media screen and (max-width: 700px) {
	.section__padding {
		padding: 4rem;
	}
	.section__margin {
		margin: 4rem;
	}
}

@media screen and (max-width: 550px) {
	.section__padding {
		padding: 4rem 2rem;
	}
	.section__margin {
		margin: 4rem 2rem;
	}
}
  