.portfolio__professional {
    display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	width: 100%;
    margin-bottom: 2rem;
    color: var(--color-text);
    font-family: var(--font-family);

}

.portfolio__professional-container-desc {
	margin-bottom: 2rem;
}

.portfolio__professional-container-desc h1 {
	font-size: 30px;
}

.portfolio__professional-container-desc p {
	font-size: 20px;
}

.portfolio__professional-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: var(--font-family);
    color: var(--color-text);
}



.portfolio__professional-container-frame {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.portfolio__professional-container-frame img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    width: calc(min(500px, 100vh) * 0.8);
    transition: transform 0.25s ease, filter 0.25s ease;
    filter: saturate(0.9);
}

.portfolio__professional-cards {
	display: flex;
}

.portfolio__professional-card-container {
    display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	padding: 1rem;
	row-gap: 1.5rem;
}


