.portfolio__navbar {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	padding: 2rem 6rem;
}

.portfolio__navbar-links {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 1rem;
}

.portfolio__navbar-links_logo {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	padding: 1rem;
}

.portfolio__navbar-links_logo img {
	width: 62.56px;
	height: 16.02px;
}

.portfolio__navbar-links_logo h1 {
	color: orange;
	font-family: var(--font-family);
	font-weight: 700;
	font-size: 50px;
	line-height: 25px;
	text-transform: capitalize;
	margin: 1rem 1rem;
	padding: 20px 20px;
	border-bottom: #ecd075 2px solid;
	word-wrap: break-word;
}

.portfolio__navbar-links_logo h2 {
	color: var(--color-text);
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 1rem;
	line-height: 25px;
	text-transform: capitalize;
	margin: 0;
	flex-wrap: wrap;
}

.portfolio__navbar-links_logo p {

}

.portfolio__navbar-links_container {
	display: flex;
	flex-direction: row;
}

.portfolio__navbar-sign {
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.portfolio__navbar-links_container p,
.portfolio__navbar-sign p,
.portfolio__navbar-menu_container p {
	color: var(--color-text);
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 25px;
	line-height: 25px;
	text-transform: capitalize;
	margin: 0 1rem;
	cursor: pointer;
}

.portfolio__navbar-links_container p:hover{
	color: #FF4820;
}

.portfolio__navbar-links_container p.inactive{
	color: var(--color-text)
}

.portfolio__navbar-links_container p.active{
	color: #FF4820;
}

.portfolio__navbar-sign button,
.portfolio__navbar-menu_container button {
	padding: 0.5rem 1rem;
	color: var(--color-text);
	background: #FF4820;
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 18px;
	line-height: 25px;
	border-radius: 5px;
	border: 0;
	outline: none;
	cursor: pointer;
}

.portfolio__navbar-menu {
	margin-left: 1rem;
	display: none;
	position: relative;
}

.portfolio__navbar-menu svg {
	cursor: pointer;
}

.portfolio__navbar-menu_container {
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	flex-direction: column;

	text-align: end;
	background: var(--color-footer);
	padding: 2rem;
	position: absolute;
	top: 40px;
	right: 0;
	margin-top: 1rem;
	min-width: 210px;
	border-radius: 5px;
	box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.portfolio__navbar-menu_container p {
	margin: 1rem 0;
}

.portfolio__navbar-menu_container-links-sign{
	display: none;
}

@media screen and (max-width: 1050px) {
	.portfolio__navbar-links_container {
		display: none;
	}

	.portfolio__navbar-menu_container p:hover {
		color: #FF4820;
	}

	.portfolio__navbar-menu {
		display: flex;
		z-index: 2;
	}
}

@media screen and (max-width: 700px) {
	.portfolio__navbar-links_logo h1 {
		font-size: 30px;
	}
	.portfolio__navbar {
		padding: 2rem 4rem;
	}
}

@media screen and (max-width: 550px) {
	.portfolio__navbar-links_logo h1 {
		font-size: 20px;
	}

	.portfolio__navbar-links_logo h2 {
		font-size: 15px;
	}
	
	.portfolio__navbar {
		padding: 2rem;
	}

	.portfolio__navbar-sign {
		display: none;
	}

	.portfolio__navbar-menu_container {
		top: 20px;
	}

	.portfolio__navbar-menu_container-links-sign{
		display: block;
	}
}