.portfolio__projects {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	width: 100%;
}

.portfolio__projects h1 {
	align-self: center;
	font-size: 25px;
	font-family: var(--font-family);
	color: var(--color-text);
}

.portfolio__projects-cards {
	display: flex;
}

.portfolio__projects-card-container {
	// width: 80%;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	padding: 1rem;
	row-gap: 1.5rem;
}

.portfolio__projects-container-desc {
	margin-bottom: 2rem;
}

.portfolio__projects-container-desc h1 {
	font-size: 30px;
}

.portfolio__projects-container-desc p {
	font-size: 20px;
}

@media screen and (max-width: 1050px) {
	.portfolio__projects-card-container {
		grid-template-columns: 1fr 1fr 1fr;
	}
}

@media screen and (max-width: 700px) {
	.portfolio__projects-card-container {
		grid-template-columns: 1fr 1fr;
	}
}

@media screen and (max-width: 550px) {
	.portfolio__projects-card-container {
		grid-template-columns: 1fr;
	}
}